/** @jsx jsx */
import { css, jsx } from "@emotion/core";

import { FormState } from "../../globals.d";

import InfoBloc from "../../components/InfoBloc";
import RecapBloc from "./components/RecapBloc";

interface Props {
  indicateurTroisFormValidated: FormState;
  indicateurTroisCalculable: boolean;
  indicateurEcartPromotion: number | undefined;
  indicateurSexeSurRepresente: "hommes" | "femmes" | undefined;
  noteIndicateurTrois: number | undefined;
}

function RecapitulatifIndicateurTrois({
  indicateurTroisFormValidated,
  indicateurTroisCalculable,
  indicateurEcartPromotion,
  indicateurSexeSurRepresente,
  noteIndicateurTrois
}: Props) {
  if (indicateurTroisFormValidated !== "Valid") {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 3, cart de taux de promotion entre les hommes et les femmes"
          text="Nous ne pouvons pas calculer votre indicateur car vous navez pas encore valid vos donnes saissies."
        />
      </div>
    );
  }

  if (!indicateurTroisCalculable) {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 3, cart de taux de promotion entre les hommes et les femmes"
          text="Malheureusement votre indicateur est incalculable car il ny a pas eu de promotion durant la priode de rfrence"
        />
      </div>
    );
  }

  return (
    <div css={styles.container}>
      <RecapBloc
        title="Indicateur 3, cart de taux de promotion entre les hommes et les femmes"
        resultBubble={{
          firstLineLabel: "votre rsultat final est",
          firstLineData:
            (indicateurEcartPromotion !== undefined
              ? indicateurEcartPromotion.toFixed(1)
              : "--") + " %",
          firstLineInfo: `cart favorable aux ${indicateurSexeSurRepresente}`,
          secondLineLabel: "votre note obtenue est",
          secondLineData:
            (noteIndicateurTrois !== undefined ? noteIndicateurTrois : "--") +
            "/15",
          secondLineInfo: "mesures de correction prises en compte"
        }}
      >
        <div />
      </RecapBloc>
    </div>
  );
}

const styles = {
  container: css({
    display: "flex",
    flexDirection: "column",
    marginTop: 22,
    marginBottom: 22
  })
};

export default RecapitulatifIndicateurTrois;
