/** @jsx jsx */
import { css, jsx } from "@emotion/core";

import { FormState } from "../../globals.d";

import InfoBloc from "../../components/InfoBloc";
import RecapBloc from "./components/RecapBloc";

interface Props {
  indicateurDeuxFormValidated: FormState;
  indicateurDeuxCalculable: boolean;
  indicateurEcartAugmentation: number | undefined;
  indicateurSexeSurRepresente: "hommes" | "femmes" | undefined;
  noteIndicateurDeux: number | undefined;
}

function RecapitulatifIndicateurDeux({
  indicateurDeuxFormValidated,
  indicateurDeuxCalculable,
  indicateurEcartAugmentation,
  indicateurSexeSurRepresente,
  noteIndicateurDeux
}: Props) {
  if (indicateurDeuxFormValidated !== "Valid") {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 2, cart de taux daugmentation entre les hommes et les femmes"
          text="Nous ne pouvons pas calculer votre indicateur car vous navez pas encore valid vos donnes saissies."
        />
      </div>
    );
  }

  if (!indicateurDeuxCalculable) {
    return (
      <div css={styles.container}>
        <InfoBloc
          title="Indicateur 2, cart de taux daugmentation entre les hommes et les femmes"
          text="Malheureusement votre indicateur est incalculable car il ny a pas eu daugmentation durant la priode de rfrence"
        />
      </div>
    );
  }

  return (
    <div css={styles.container}>
      <RecapBloc
        title="Indicateur 2, cart de taux daugmentation entre les hommes et les femmes"
        resultBubble={{
          firstLineLabel: "votre rsultat final est",
          firstLineData:
            (indicateurEcartAugmentation !== undefined
              ? indicateurEcartAugmentation.toFixed(1)
              : "--") + " %",
          firstLineInfo: `cart favorable aux ${indicateurSexeSurRepresente}`,
          secondLineLabel: "votre note obtenue est",
          secondLineData:
            (noteIndicateurDeux !== undefined ? noteIndicateurDeux : "--") +
            "/20",
          secondLineInfo: "mesures de correction prises en compte"
        }}
      >
        <div />
      </RecapBloc>
    </div>
  );
}

const styles = {
  container: css({
    display: "flex",
    flexDirection: "column",
    marginTop: 22,
    marginBottom: 22
  })
};

export default RecapitulatifIndicateurDeux;
